import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { useLoader } from 'app/utils/loader/decorators';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { InstallationService } from '../installation.service';

@Component({
    templateUrl: './installation-start.component.html',
    styleUrls: ['./installation-start.component.scss'],
})
export class InstallationStartComponent {
    constructor(
        private router: Router,
        private route: ActivatedRoute,
        private loader: LoaderDirective,
        private installationService: InstallationService,
    ) {}

    @useLoader
    async start() {
        await this.installationService.start();
        this.router.navigate(['progress'], { relativeTo: this.route });
    }
}
