import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { InstallationGuard } from './installation.guard';
import { InstallationComponent } from './installation.component';
import { InstallationLogComponent } from './log/installation-log.component';
import { InstallationSliderComponent } from './slider/installation-slider.component';
import { InstallationStartComponent } from './start/installation-start.component';
import { InstallationTabsComponent } from './tabs/installation-tabs.component';
import { StoppedServiceComponent } from './stopped-service/stopped-service.component';


const routes: Routes = [{
    path: '',
    component: InstallationComponent,
    canActivate: [InstallationGuard],
    children: [{
        path: '',
        pathMatch: 'full',
        component: InstallationStartComponent,
    }, {
        path: 'stopped-service',
        component: StoppedServiceComponent,
    }, {
        path: 'progress',
        component: InstallationTabsComponent,
        children: [{
            path: '',
            pathMatch: 'full',
            redirectTo: 'slides',
        }, {
            path: 'log',
            component: InstallationLogComponent,
        }, {
            path: 'slides',
            component: InstallationSliderComponent,
        }],
    }],
}];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule],
})
export class InstallationRoutingModule {
}
