import { Component, ElementRef, Input, ViewChild } from '@angular/core';


export type LogChunk = {
    text: string;
};


@Component({
    selector: 'i360-log-box',
    templateUrl: './log-box.component.html',
    styleUrls: ['./log-box.component.scss'],
})
export class LogBoxComponent {
    @Input() set log(log: LogChunk[]) {
        this._log = log;
        setTimeout(() => this.scrollToBottom());
    }
    get log() {
        return this._log;
    }
    @ViewChild('logOutput', {static: true}) logOutput: ElementRef<HTMLDivElement>;
    private _log: LogChunk[];
    scrollToBottom() {
        this.logOutput.nativeElement.scrollTop = this.logOutput.nativeElement.scrollHeight;
    }
}
