import { Component, OnDestroy, OnInit } from '@angular/core';
import { AgentStatus } from '@imunify360-api/misc';
import { AppState } from 'app/core/app.service';
import { InstallationService } from 'app/components/installation/installation.service';


@Component({
    selector: 'installation-log',
    templateUrl: './installation-log.component.html',
    styleUrls: ['./installation-log.component.scss'],
})
export class InstallationLogComponent implements OnInit, OnDestroy {
    AgentStatus = AgentStatus;
    constructor(
        public appState: AppState,
        public installationService: InstallationService,
    ) {}

    ngOnInit(): void {
        this.installationService.startWatchingLog();
    }

    ngOnDestroy(): void {
        this.installationService.stopWatchingLog();
    }
}
