import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { timer, BehaviorSubject, Subscription, catchError, repeatWhen, switchMap, tap } from 'rxjs';
import { AgentStatus } from '@imunify360-api/misc';
import { AppState } from 'app/core/app.service';
import { InstallationBackendService } from './installation-backend.service';
import { LogChunk } from 'app/utils/log-box/log-box.component';


export const LOG_REFRESH_INTERVAL = 1000;
export const LOG_CHUNK_SIZE = 1024 * 1024;

@Injectable()
export class InstallationService {
    accumulatedLog = new BehaviorSubject<LogChunk[]>([]);
    shownSlidesCount = 0;
    private logCollector$ = timer(0).pipe(
        switchMap(() => this.installationBackendService.status({
            offset: this.offset,
            limit: LOG_CHUNK_SIZE,
        })),
        tap(res => {
            const items = res.data.items;
            const offsetDiff = items.offset - this.offset;
            this.offset = items.offset;
            const chunk = items.log;
            if (chunk) {
                this.accumulatedLog.next(this.accumulatedLog.getValue().concat([{text: chunk}]));
            }
            if (offsetDiff === 0 && this.appState.imunifyStatus.value !== AgentStatus.INSTALLING) {
                this.stopWatchingLog();
            }
            if (this.appState.imunifyStatus.value === AgentStatus.OK) {
                this.router.navigate(['/'], {
                    replaceUrl: true,
                });
            }
        }),
        catchError(() => timer(LOG_REFRESH_INTERVAL)),
        repeatWhen(notifications => {
            return notifications.pipe(
                switchMap(() => timer(LOG_REFRESH_INTERVAL)),  // needed for unit tests
            );
        }),
    );
    private logCollectorSubscription = Subscription.EMPTY;
    private offset = 0;

    constructor(
        private router: Router,
        private appState: AppState,
        private installationBackendService: InstallationBackendService,
    ) {}

    start() {
        this.offset = 0;
        this.accumulatedLog.next([]);
        return this.installationBackendService.start();
    }

    startWatchingLog() {
        this.logCollectorSubscription = this.logCollector$.subscribe();
    }

    stopWatchingLog() {
        this.logCollectorSubscription.unsubscribe();
    }
}
