import { AuthState } from 'app/services/auth-state';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { timer, EMPTY, Subject, catchError, repeatWhen, switchMap, takeUntil } from 'rxjs';
import { LicenseAPI, Package } from '@imunify360-api/license';
import { AppState } from 'app/core/app.service';
import { XHR } from 'app/utils/xhr/xhr';

@Component ({
    selector: 'stopped-service',
    template: `
        <div class="i360-stopped-wrapper">
            <blocked-ui-message [title]="'stoppedService.title' | translate">
                <div class="message-text" *ngIf="authState.isAdmin|async">
                    {{ 'stoppedService.message' | translate }}
                    <a class="i360-dashboard-link"
                       [href]="is360Package ? docsI360Link : docsAvLink"
                       target="_blank">
                        {{ 'stoppedService.guide' | translate }}
                    </a>
                    {{ 'stoppedService.or' | translate }}
                    <a class="i360-dashboard-link"
                       href="https://cloudlinux.zendesk.com/hc/requests/new"
                       target="_blank">
                        {{ 'stoppedService.support' | translate }}
                    </a>
                </div>
            </blocked-ui-message>
        </div>
    `,
    styleUrls: ['./stopped-service.component.scss'],
})
export class StoppedServiceComponent implements OnInit, OnDestroy {
    is360Package = IMUNIFY_PACKAGE === Package.imunify360;
    readonly docsAvLink = 'https://docs.imunifyav.com/faq_and_known_issues' +
        '/#imunify-agent-is-not-running-troubleshooting';
    readonly docsI360Link = 'https://docs.imunify360.com/faq_and_known_issues/' +
        '#_23-imunify-agent-is-not-running-troubleshooting';
    private destroyed = new Subject<void>();

    constructor(
        public authState: AuthState,
        public appState: AppState,
        private xhr: XHR,
    ) {}

    ngOnInit(): void {
        timer(0).pipe(
            switchMap(() => this.xhr.post(LicenseAPI.version())),
            catchError(() => EMPTY),
            repeatWhen((notifications) => notifications.pipe(
                switchMap(() => timer(10000)),
            )),
            takeUntil(this.destroyed),
        ).subscribe();
    }

    ngOnDestroy(): void {
        this.destroyed.next();
        this.destroyed.complete();
    }
}

