import {
    Component, OnDestroy, OnInit, QueryList, TemplateRef, ViewChildren, ViewEncapsulation,
} from '@angular/core';
import { animate, style, transition, trigger } from '@angular/animations';
import { LicenseAPI, Package } from '@imunify360-api/license';
import {
    timer, EMPTY, Subject,
    catchError, map, repeatWhen, switchMap, takeUntil, tap
} from 'rxjs';
import { AuthState } from 'app/services/auth-state';
import { XHR } from 'app/utils/xhr/xhr';
import { InstallationService } from 'app/components/installation/installation.service';
import { AppState } from 'app/core/app.service';


export const REFRESH_INTERVAL = 20000;
export const ANIMATION_DURATION = REFRESH_INTERVAL * 0.15;
export const STATUS_POLLING_INTERVAL = 5000;

@Component ({
    selector: 'installation-slider',
    templateUrl: './installation-slider.component.html',
    styleUrls: ['./installation-slider.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations: [ // infinity css animation is more simple but it
        // can be not synchronized with timer inside component
        trigger(
            'enterAnimation', [
                transition(':enter', [
                    style({ opacity: 0 }),
                    animate(`${ANIMATION_DURATION}ms`, style({ opacity: 1 })),
                    animate(
                        `${ANIMATION_DURATION}ms ${REFRESH_INTERVAL - 2 * ANIMATION_DURATION}ms`,
                        style({ opacity: 0 }),
                    ),
                ]),
            ],
        ),
    ],
})
export class InstallationSliderComponent implements OnInit, OnDestroy {
    @ViewChildren('installationSlide') slides: QueryList<TemplateRef<any>>;
    activeSlide$ = timer(0, REFRESH_INTERVAL).pipe(
        map(() => {
            const slideNumber = this.installationService.shownSlidesCount % this.slides.length;
            return this.slides.toArray()[slideNumber];
        }),
        tap(() => {
            this.installationService.shownSlidesCount += 1;
        }),
    );
    isAv = IMUNIFY_PACKAGE === Package.imunifyAV;

    private destroyed = new Subject<void>();

    constructor(
        public authState: AuthState,
        private xhr: XHR,
        private appState: AppState,
        private installationService: InstallationService,
    ) {}

    ngOnInit(): void {
        timer(0).pipe(
            switchMap(() => this.xhr.post(LicenseAPI.version())),
            catchError(() => EMPTY),
            repeatWhen((notifications) => notifications.pipe(
                switchMap(() => timer(STATUS_POLLING_INTERVAL)),
            )),
            takeUntil(this.destroyed),
        ).subscribe();
    }

    ngOnDestroy(): void {
        // needed to display the last shown slide when switching between the tabs
        this.installationService.shownSlidesCount -= 1;

        this.destroyed.next();
        this.destroyed.complete();
    }
}
